@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
#landing {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    background: radial-gradient(circle, rgb(89, 0, 148) 33%, rgb(0,0,0) 100%);
    object-fit: cover;
    background-repeat: no-repeat;
    height: 100%;
    animation: Fade_In 1s ease;
}

.fade {
    height: 100%;
    width: 100%;
    display: block;
    position: relative;

}


#slogan {
    position: absolute;
    margin: auto;
    font-size: 75px;
    color: white;
    transform: translate(3.3%);
    letter-spacing: 2px;
    animation: Squish 1s ease;
}

.tm {
    font-size: 16px;
    vertical-align: top;
    margin-top: 10px;
}

#logo-img {
    height: 75vh;
    animation: Slide_Up 1s ease;
}

#login-btn {
    padding: 7px 20px 7px 20px;
    background:white;
    color:rgba(226, 43, 255, 1)
}

#signup-btn {
    padding: 10px 20px 10px 20px;
    background:white;
    color:rgba(226, 43, 255, 1)
}

#signup-btn:hover, #login-btn:hover{
    background: radial-gradient(
    circle,
    rgba(104, 63, 255, 1) 0%,
    rgba(226, 43, 255, 1) 100%
    );
    color: white;
    animation: Hover .5s ease;
}

.login-modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(255, 255, 255, 0.75);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    opacity: 1;
    animation: Fade_In .5s ease;
}

.login-modal {
    display: flex;
    flex-direction: column;
    width: 50%;
    max-width: 350px;
    color: black;
    font-family: "Quicksand", sans-serif;
    justify-content: space-around;
    background: white;
    background-size: 100%;
    background-repeat: repeat;
    padding: 2rem;
    text-align: center;
    border-radius: 25px;
    opacity: 1;
    box-shadow: 2px 2px 10px 1px #a535ff;
    animation: Slide_Up .5s ease;
}

.login-modal h2 {
    font-size: 42px;
    margin-bottom: 1rem;
}

.login-modal h3 {
    margin-bottom: 15px;
}

.login-modal p {
    margin: 5px;
}

.modal-in {
    margin: 5px; 
    border-radius: 15px;
    border: 1px solid #a435ff;
    font-size: 24px;
    text-align: center;
    font-family: 'Quicksand'; 
}

.submit-btn {
    margin: 5px;
    background: radial-gradient(
    circle,
    rgba(104, 63, 255, 1) 0%,
    rgba(226, 43, 255, 1) 100%
  );
  border: none;
  border-radius: 15px;
  color: white;
  font-size: 24px;
  box-shadow: gray 1px 1px 2px;
  font-weight: 500;
  font-family: 'Quicksand';
}

@keyframes Fade_In {
    from {opacity: 0;}
    to {opacity: 1;}
}

@keyframes Slide_Up {
    from {opacity: 0;}
    to {opacity: 1;}
    0% {
        transform: translateY(60%);
    }
    100% {
        transform: translateY(0);
    }
}

@keyframes Squish {
    from {letter-spacing: 25px;}
    to {letter-spacing: 2px;}
}

@keyframes Hover {
    from {
        background: white;
        color: rgba(226, 43, 255, 1);
    }

    to { 
        background: radial-gradient(
        circle,
        rgba(104, 63, 255, 1) 0%,
        rgba(226, 43, 255, 1) 100%
        );
        color: white;
    }

}
